import { Wrapper, Span } from '../../Elements';
import styled, { css } from 'styled-components';
import { pixelToRem } from 'meema.utils';
const baseColor = '#C1C1C1';
export const Main = styled(Wrapper) `
  position: relative;
  display: flex;
  flex-direction: ${props => props.alignYLabel === 'top' ? 'column-reverse' : 'column'};
  justify-content: space-between;
  transition: all 250ms ease;
  width: 100%;
  justify-content: flex-end;
 
  &:first-child {
    ${props => props.alignCircle && css `
      ${props.alignCircle === 'center' && css `
        width: 50%;
      `}
    `}
    
    .progress-line.inProgress {
      background: ${(props) => props.theme ? props.theme.color.primary.normal : '#999999'};
    }
    
    .circle {
      ${props => props.alignCircle && css `
        ${props.alignCircle === 'center' && css `
          left: 0;
        `}
      `}
    }
    
    .label {
      text-align: ${props => props.alignXLabel ? props.alignXLabel : 'left'};
    }
  }
  
  &:last-child {
    ${props => props.alignCircle && css `
      ${props.alignCircle === 'center' && css `
        width: 50%;
      `}
    `}

    .progress-line.inProgress {
      background: ${(props) => props.theme ? props.theme.color.primary.normal : '#5DCFC6'};
    }
    .circle {
      ${props => props.alignCircle && css `
        ${props.alignCircle === 'center' && css `
          left: ${`calc(100% - ${pixelToRem(30)})`};
        `}
      `}
    }
    .label {
      text-align: ${props => props.alignXLabel ? props.alignXLabel : 'right'};
    }
  }

  &:nth-child(odd) {
    /* background: orange; */
  }
`;
export const ProgressLine = styled(Wrapper) `
  height: 5px;
  flex: 0 0 100%;
  display: block;
  transition: all 250ms ease;
  background: ${baseColor};

  ${props => props.status && css `
    ${props.status === 'inProgress' && css `
      background: linear-gradient(
        90deg,
        ${props => props.theme ? props.theme.color.primary.normal : '#5DCFC6'} 50%,
        #999999 50%
        );
    `}
    ${props.status === 'done' && css `
      background: linear-gradient(
        90deg,
        ${props => props.theme ? props.theme.color.primary.normal : '#5DCFC6'} 50%,
        ${props => props.theme ? props.theme.color.primary.normal : '#5DCFC6'} 50%
      );
    `}
  `}
`;
export const Circle = styled(Wrapper) `
  position: absolute;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  left: calc(50% - 15px);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #C1C1C1;
  transition: all 250ms ease;

  ${props => props.alignCircle && css `
    ${props.alignCircle === 'left' && css `
      left: 0;
    `}

    ${props.alignCircle === 'right' && css `
      left: ${`calc(100% - ${pixelToRem(30)})`};
    `}
  `}

  ${props => props.status && css `
    ${props.status === 'inProgress' && css `
      /* background: ${props => props.theme && props.theme.color.primary.normal}; */
    `}
    ${props.status === 'done' && css `
      background: ${props => props.theme && props.theme.color.primary.normal};
    `}
  `}  
`;
export const Label = styled(Span) `
  font-size: 14px;
  ${props => (props.alignYLabel && props.alignYLabel === 'top')
    ? css `margin-bottom: 20px;`
    : css `margin-top: 20px;`};
`;
